  flex-direction: column;
}
@media (max-width: 1140px) {
  .workflow__list-item {
    position: relative;
    padding-left: 60px;
    padding-bottom: 40px;
    padding-top: 5px;
  }
  .workflow__list-item::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 30px;
    height: 30px;
    content: "";
    background-color: var(--purple-3);
    border: 2px solid var(--purple-3);
    box-shadow: inset 0 0 0 8px var(--primary);
    border-radius: 50%;
  }
  .workflow__list-item:not(:last-child)::after {
    position: absolute;
    top: 30px;
    left: 14px;
    width: 2px;
    height: calc(100% - 30px);
    content: "";
    background-color: var(--purple-3);
  }
}
.workflow__list-item-title {
  font-size: 22px;
  font-weight: 400;
  line-height: 1.1;
  margin: 0;
  margin-bottom: 16px;
}
.workflow__list-item-text {
  font-weight: 300;
  margin: 0;
}
.workflow__list-item:nth-child(even) {
  justify-content: flex-end;
}
@media (max-width: 1140px) {
  .workflow__list-item:nth-child(even) {
    justify-content: flex-start;
  }
}
.workflow__list-item:nth-child(odd) {
  grid-row: 2/3;
}
.workflow__list-item:nth-child(even) {
  grid-row: 1/2;
}
.workflow__list-item:nth-child(1) {
  grid-column: 1/6;
}
.workflow__list-item:nth-child(2) {
  grid-column: 3/8;
}
.workflow__list-item:nth-child(3) {
  grid-column: 6/11;
}
.workflow__list-item:nth-child(4) {
  grid-column: 8/13;
}
.workflow__list-item:nth-child(5) {
  grid-column: 11/16;
}
.workflow__list-item:nth-child(6) {
  grid-column: 13/18;
}

.contacts__content {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-gap: 40px;
}
.contacts__field {
  font-family: var(--alt-font);
}
.contacts__field span {
  display: inline-block;
  font-size: 18px;
  margin-bottom: 12px;
}
.contacts__field a {
  font-weight: 700;
  font-size: 40px;
  color: var(--white);
}

.clients-slider .swiper-slide {
  padding: 15px;
  border-radius: 2px;
  width: fit-content;
  min-width: 160px;
  height: 70px;
  background-color: var(--white);
}
.clients-slider .swiper-slide a {
  height: 100%;
  width: 100%;
}
.clients-slider .swiper-slide img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.clients-slider .swiper-slide.dark-bg {
  background-color: var(--lightblack);
}

.callback {
  background-color: var(--alt-bg-color);
}

